    height: 100%;
}

body {
    font-family: sans-serif;
    color: #666;
    margin: 0;
}

#root {
    display: flex;
    flex-direction: column;
}
#root > div.graph {
    flex: 1;
    overflow: auto;
    position: relative;
}

.toolbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 4px 10px;
    background-color: #f0f0f0;
    border-bottom: 1px solid #cccccc;
}

.toolbar > div {
    display: flex;
    align-items: center;
}

.toolbar button {
    padding: 5px 8px;
    margin: 0 2px;
    cursor: pointer;
}

.toolbar button:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

/* Drag mode toggle button styles */
.toolbar button.mode-toggle {
    position: relative;
    border: 1px solid #8f9fc9;
    width: 40px;
    min-height: 28px;
    background: linear-gradient(to bottom, #abb8db, #8f9fc9);
    border-color: #8f9fc9;
    color: white;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, .2);
}

.toolbar button.mode-toggle:hover {
    background: linear-gradient(to bottom, #bcc7e0, #abb8db);
    border-color: #abb8db;
}

.toolbar button.mode-toggle.select-mode:active {
    background: linear-gradient(to bottom, #8f9fc9, #7a8bb5);
}

/* Pan mode and active toggle - darker blue */
.toolbar button.mode-toggle.pan-mode,
.toolbar button.active-toggle {
    background: linear-gradient(to bottom, #4a90e2, #357abd);
    border-color: #2968a3;
    box-shadow: inset 0 1px 2px rgba(0,0,0,0.2);
}

.toolbar button.mode-toggle.pan-mode:hover,
.toolbar button.active-toggle:hover {
    background: linear-gradient(to bottom, #5ba0f2, #4585c7);
}

.toolbar button.mode-toggle.pan-mode:active {
    background: linear-gradient(to bottom, #357abd, #2968a3);
    box-shadow: inset 0 1px 2px rgba(0,0,0,0.2);
}

/* Toggle buttons when inactive - gray styling */
.toolbar button.inactive-toggle {
    background: #c1c1c1;
    color: #999;
    border-color: #c1c1c1;
    box-shadow: none;
}

.toolbar button.inactive-toggle:hover {
    background: #b1b1b1;
}

.toolbar button.inactive-toggle:active {
    background: #a1a1a1;
}

/* Toggle buttons when disabled and not active - gray like other disabled buttons */
.toolbar button.mode-toggle:disabled:not(.pan-mode):not(.active-toggle),
.toolbar button.active-toggle:disabled:not(.active-toggle) {
    background: #c1c1c1;
    color: #999;
    border-color: #c1c1c1;
    box-shadow: none;
}

/* Ensure Font Awesome icons are sized appropriately if not already handled */
.toolbar button .fas {
    font-size: 1em;
    vertical-align: middle;
}

/* Zoom percentage display */
.toolbar button.zoom-display {
    font-family: 'SF Mono', Monaco, 'Roboto Mono', Consolas, monospace;
    font-size: 11px;
    font-weight: 600;
    min-width: 50px;  /* Wider to prevent size change between 99% and 100% */
    padding: 6px 8px;
    text-align: center;
}

.toolbar-group {
    display: flex;
    align-items: center;
    margin-right: 25px; /* Large space between groups */
}

.toolbar-group:last-child {
    margin-right: 0; /* Remove right margin from the last group (help button) */
}

button {
    border: none;
    background: linear-gradient(to bottom, #4a90e2, #357abd);
    border-color: #2968a3;
    border-radius: 3px;
    padding: 6px 10px;
    color: #fff;
    outline: none;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, .2);
    margin-right: 5px;
    min-width: 32px;
    min-height: 28px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    line-height: 1;
    font-family: "Segoe UI", "Segoe UI Symbol", "Segoe UI Emoji", system-ui, -apple-system, BlinkMacSystemFont, "Helvetica Neue", Arial, sans-serif;
    position: relative;
}

button:hover {
    background: linear-gradient(to bottom, #5ba0f2, #4585c7);
}

button:active {
    background: linear-gradient(to bottom, #357abd, #2968a3);
}

button:last-child {
    margin-right: 0;
}

/* Save button when no changes - gray */
button.action {
    background: #c1c1c1;
    color: #999;
}

button.action:active {
    background: #b1b1b1;
}

/* Save button when there are changes - orange */
button.grp {
    background: linear-gradient(to bottom, #ee9564, #de7d48);
    margin-right: 0;
}

button.grp:active {
    background: #d67540;
}

select {
    border: 1px solid #ccc;
    background: white;
    border-radius: 3px;
    padding: 3px 7px;
    color: #666;
    outline: none;
    margin-right: 5px;
    font-size: 12px;
}

select:disabled {
    background: #f5f5f5;
    color: #999;
}

button:disabled {
    background: #c1c1c1;
    color: #999;
}

#root > div > svg {
    position: absolute;
    user-select: none;
}


.node.selected .nodeBorder, .edge.selected path, .edge.selected rect {
    stroke: #29c229;
}
.edge .v-dot {
    fill: transparent;
    stroke: transparent;
    stroke-width: 3px;
    cursor: pointer;
    transition: stroke 0.15s ease;
}
.edge .v-dot:hover {
    stroke: #999;
    fill: rgba(153, 153, 153, 0.1);
}
.edge .v-dot.selected {
    stroke: #29c229;
    fill: rgba(41, 194, 41, 0.1);
}
.edge .v-dot.selected:hover {
    stroke: #29c229;
    fill: rgba(41, 194, 41, 0.2);
}
.edge .v-dot.auto.selected {
    stroke: #777;
    fill: rgba(119, 119, 119, 0.1);
}
.edge .v-dot.auto.selected:hover {
    stroke: #777;
    fill: rgba(119, 119, 119, 0.2);
}
circle#prj {
    fill: none;
    stroke: #777;
}

.nodeShadow {
    fill: none;
    stroke-width: 4px;
    stroke: rgba(0, 0, 0, 0.13);
}

g.node {
    user-select: none;
    cursor: default;
}

g.node text {
    pointer-events: none;
}

.icon {
    fill: #aaa;
    stroke: #fff;
}
#icon-cube {
    fill: #aaa;
}

/* Ensure all button icons are uncolored */
button .icon,
button svg,
button path {
    fill: currentColor !important;
    stroke: none !important;
}

/* Font Awesome icon styling in buttons */
button i {
    font-size: 12px;
    color: inherit;
}

rect.elastic {
    pointer-events: none;
    stroke: none;
    fill: #3bd8281f;
    display: none;
}
rect.elastic.on {
    display: block;
}

.popover {
    position: absolute;
    top: 50px;
    bottom: 10px;
    overflow: auto;
    right: 10px;
    background: ghostwhite;
    padding: 30px;
    box-shadow: 3px 3px 5px rgba(0,0,0, .2);
    border: solid 1px #eee;
}

.popover th {
    text-align: left;
    padding: 20px 0px 10px;
}
.popover td {
    padding-right: 20px;
    font-size: 14px;
}

/* Simple tooltip system with smart positioning */
[data-tooltip] {
    position: relative;
}

[data-tooltip]:hover::after {
    content: attr(data-tooltip);
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    background: rgba(0, 0, 0, 0.9);
    color: white;
    padding: 6px 12px;
    border-radius: 4px;
    font-size: 12px;
    white-space: nowrap;
    z-index: 1000;
    pointer-events: none;
    margin-top: 5px;
    animation: tooltip-appear 0.1s ease-out;
    min-width: 120px;
    max-width: calc(100vw - 20px);
    box-sizing: border-box;
}

[data-tooltip]:hover::before {
    content: '';
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    border: 4px solid transparent;
    border-bottom-color: rgba(0, 0, 0, 0.9);
    z-index: 1000;
    pointer-events: none;
    margin-top: 1px;
    animation: tooltip-appear 0.1s ease-out;
}

/* Special handling for rightmost elements that might overflow */
.toolbar-group:nth-last-child(-n+2) button[data-tooltip]:hover::after {
    /* Apply to last 2 toolbar groups (save and help buttons) */
    left: auto;
    right: 0;
    transform: none;
}

.toolbar-group:nth-last-child(-n+2) button[data-tooltip]:hover::before {
    /* Apply to last 2 toolbar groups (save and help buttons) */
    left: auto;
    right: 16px;
    transform: none;
}

@keyframes tooltip-appear {
    from {
        opacity: 0;
        transform: translateX(-50%) translateY(-5px);
    }
    to {
        opacity: 1;
        transform: translateX(-50%) translateY(0);
    }
}

